import React from "react";
import logo from "./logo.svg";
import "./App.css";
// import { start } from "photo2pdf/packages/js-wrapper";
import { start } from "frontend-photos-to-pdf/packages/js-wrapper";
import "frontend-photos-to-pdf/packages/react-components/styles.css";

function App() {
  // React.useEffect(() => {
  //   console.log("START IN MVP");

  //   const onCompleted = (outputs) => {
  //     alert("sucessfully created pdf, see console and opened pdf");
  //     console.log(outputs);
  //     const pdfURL = outputs.pdf;
  //     window.open(pdfURL, "_blank");
  //   };
  //   const onAborted = () => {
  //     alert("process aborted, closed lib");
  //   };
  //   start({
  //     process_id: 1,
  //     document_id: 2,
  //     filename: "testfile.pdf",
  //     onCompleted,
  //     onAborted,
  //   });
  // }, []);

  const onCompleted = (outputs) => {
    console.log("sucessfully created pdf, see console and opened pdf");
    setOutputs(outputs);
    // console.log(outputs);
    // const pdfURL = outputs.pdf;
    // window.open(pdfURL, "_blank");
  };
  const onAborted = () => {
    alert("process aborted, closed lib");
  };

  const [inputs, setInputs] = React.useState(() => ({
    process_id: 1,
    document_id: 2,
    filename: "testfile.pdf",
    onCompleted,
    onAborted,
  }));
  const [outputs, setOutputs] = React.useState(() => ({
    process_id: "",
    document_id: "",
    filename: "",
    images: [],
    pdf: null,
    amountPages: 0,
  }));

  const [ui, setUi] = React.useState({
    pageIdx: 0,
    outputImageType: "croppedImage", // as "source" |"cropped",
  });

  const startLibrary = React.useCallback(() => {
    start(inputs);
  }, [inputs]);

  const reStartLibrary = React.useCallback(() => {
    const newInputs = { ...outputs, onCompleted, onAborted };
    start(newInputs);
  }, [outputs]);

  const handleChange = React.useCallback((e) => {
    const { name, value } = e?.target ?? {};
    setInputs((current) => ({ ...current, [name]: value }));
  }, []);

  const handleOpenPdfFile = React.useCallback(() => {
    const pdfURL = outputs.pdf;
    if (!pdfURL) return;
    window.open(pdfURL, "_blank");
  });

  const handleDownloadPdfFile = React.useCallback(() => {
    const url = outputs?.pdf;
    const link = document.createElement("a");
    link.href = url;
    link.setAttribute("download", outputs?.filename ?? "file.pdf");
    document.body.appendChild(link);
    link.click();
    document.body.removeChild(link);
  }, [outputs?.filename, outputs?.pdf]);

  const handleToggleImageType = React.useCallback(() => {
    setUi((current) => ({
      ...current,
      outputImageType:
        current?.outputImageType === "croppedImage" ? "image" : "croppedImage",
    }));
  }, []);

  const handlePageBack = React.useCallback(() => {
    setUi((current) => ({
      ...current,
      pageIdx: current?.pageIdx > 0 ? current?.pageIdx - 1 : 0,
    }));
  }, []);
  const handlePageForward = React.useCallback(() => {
    setUi((current) => ({
      ...current,
      pageIdx:
        current.pageIdx < (outputs?.images?.length ?? 0) - 1
          ? current.pageIdx + 1
          : outputs?.images?.length - 1,
    }));
  }, [outputs?.images?.length]);

  return (
    <div className="App">
      <div
        style={{
          maxWidth: 1024,
          margin: "16px auto",
          textAlign: "left",
          padding: "16px",
          overflowY: "auto",
          height: "100%",
        }}
      >
        {/* inputs */}
        <div style={{ marginTop: "16px", marginBottom: "8px" }}>Inputs</div>
        <div
          style={{
            display: "grid",
            gridTemplateColumns: "auto auto auto",
            justifyItems: "flex-start",
          }}
        >
          <div>
            <div>
              <label
                htmlFor="process_id"
                style={{ textAlign: "left", display: "block", width: "100%" }}
              >
                Process ID
              </label>
            </div>
            <input
              id="process_id"
              name="process_id"
              value={inputs.process_id}
              onChange={handleChange}
            />
          </div>
          <div>
            <div>
              <label
                htmlFor="document_id"
                style={{ textAlign: "left", display: "block", width: "100%" }}
              >
                Document ID
              </label>
            </div>
            <input
              id="document_id"
              name="document_id"
              value={inputs.document_id}
              onChange={handleChange}
            />
          </div>
          <div>
            <div>
              <label
                htmlFor="document_id"
                style={{ textAlign: "left", display: "block", width: "100%" }}
              >
                Filename
              </label>
            </div>
            <input
              id="filename"
              name="filename"
              value={inputs.filename}
              onChange={handleChange}
            />
          </div>
        </div>
        {/* Start */}
        <div style={{ marginTop: 16 }}>
          <button onClick={startLibrary}>Starte Library</button>
        </div>
        {/* Outputs */}
        <div style={{ marginTop: "48px", marginBottom: "8px" }}>Outputs</div>
        <div
          style={{
            display: "grid",
            gridTemplateColumns: "auto auto auto",
            justifyItems: "flex-start",
            gap: "16px 0",
          }}
        >
          <div>
            <div>
              <label
                htmlFor="process_id"
                style={{ textAlign: "left", display: "block", width: "100%" }}
              >
                Process ID
              </label>
            </div>
            <input
              name="process_id"
              value={outputs.process_id}
              disabled={true}
            />
          </div>
          <div>
            <div>
              <label
                htmlFor="document_id"
                style={{ textAlign: "left", display: "block", width: "100%" }}
              >
                Document ID
              </label>
            </div>
            <input
              name="document_id"
              value={outputs.document_id}
              disabled={true}
            />
          </div>
          <div>
            <div>
              <label
                htmlFor="document_id"
                style={{ textAlign: "left", display: "block", width: "100%" }}
              >
                Filename
              </label>
            </div>
            <input name="filename" value={outputs.filename} disabled={true} />
          </div>

          <div>
            <div>
              <label
                htmlFor="document_id"
                style={{ textAlign: "left", display: "block", width: "100%" }}
              >
                Amount Pages
              </label>
            </div>
            <input
              name="amountPages"
              value={outputs.amountPages}
              disabled={true}
            />
          </div>
        </div>
        {!!outputs.images.length && (
          <>
            <div style={{ paddingTop: 16 }}>Image Preview</div>
            <div style={{ marginTop: "16px" }}>
              <img
                src={
                  outputs?.images?.[ui?.pageIdx ?? 0]?.[
                    ui?.outputImageType ?? "images"
                  ]?.src
                }
                width={340}
                height="auto"
                alt="preview"
              />
            </div>
            <div>
              <div
                style={{
                  display: "grid",
                  gridTemplateColumns: "32px 32px max-content max-content",
                  gap: 8,
                  marginTop: 4,
                }}
              >
                <button
                  onClick={handlePageBack}
                  disabled={!ui?.pageIdx}
                >{`<`}</button>
                <button
                  onClick={handlePageForward}
                  disabled={ui?.pageIdx >= outputs.images.length - 1}
                >{`>`}</button>
                <div>{`${ui?.pageIdx + 1} / ${outputs.images.length}`}</div>

                <button onClick={handleToggleImageType}>{`Switch to ${
                  ui?.outputImageType === "croppedImage"
                    ? "Source-Image"
                    : "Cropped image (A4)"
                }`}</button>
              </div>
            </div>
            {!!outputs?.pdf && (
              <>
                <div style={{ marginTop: 16 }}>
                  <button onClick={handleOpenPdfFile}>Open Pdf</button>
                </div>
                <div style={{ marginTop: 16 }}>
                  <button onClick={handleDownloadPdfFile}>Download Pdf</button>
                </div>
                <div style={{ marginTop: 16 }}>
                  <button onClick={reStartLibrary}>
                    Restart Library with Output
                  </button>
                </div>
              </>
            )}
          </>
        )}
      </div>
    </div>
  );
}

export default App;
